


































import { Tags } from '@/components/widgets'
import { OpenAccount, UploadIdCard, AccountDetail } from '@/components/views/account/lmAccount';
// import ListTag from '@/components/views/user/mine/ListTag.vue'
import { Component, Vue } from 'vue-property-decorator';
@Component({
  components: { Tags },
})
export default class ViewComponent extends Vue {
  /**data */
  userInfo: any = {};
  isShowOpenAccountTip = false;

  // 辅助展示字段
  propTags = {
    components: [
      {
        'items': [
          {
            path: '/user/invested',
            text: '已投项目'
          },
          {
            path: '/user/coupon',
            text: '优惠券'
          },
        ]
      },
    ]
  }


  /**life hook */
  created() {
    this.appAccountStatus()
    this.appAccount()
  }

  /**method */
  async appAccountStatus() {
    const data = await this.$request({
      api: 'appAccountStatus',
      params: {
        token: this.$store.state.token,
      }
    })

    if (data.resCode === '0000' && data.lmOpenAccountStatus !== 'SUCCESS') {
      this.isShowOpenAccountTip = true
    }
  }
  async appAccount() {
    const data = await this.$request({
      api: 'appAccount',
      params: {
        token: this.$store.state.token,
      }
    })
    if (data.resCode === '0000') {
      this.userInfo = data;
    }
  }
  logout() {
    this.$store.commit('resetAll')
    this.$router.push({ path: '/user/login' })
  }
}
